.professional {
  margin: 0;
}
.professionalCarDriversContainer {
  position: relative;
  line-height: 23.88px;
  text-transform: uppercase;
  font-weight: 600;
}
.div {
  flex: 1;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: center;
  gap: 23.95px;
  min-width: 292.5813293457031px;
}
.orderAvailable {
  position: relative;
  line-height: 23.88px;
  font-weight: 600;
}
.div1 {
  flex: 1;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: center;
  gap: 23.95px;
  min-width: 292.5813293457031px;
  text-align: left;
}
.shield1Icon {
  position: relative;
  width: 41.78px;
  height: 41.78px;
  overflow: hidden;
  flex-shrink: 0;
}
.safeDrive {
  position: relative;
  line-height: 24.99px;
  text-transform: uppercase;
  font-weight: 600;
}
.div2 {
  flex: 1;
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: center;
  gap: 23.95px;
  min-width: 292.5813293457031px;
  font-size: 15.62px;
  font-family: var(--font-titillium-web);
}
.competitivePrice {
  position: relative;
  line-height: 24.99px;
  text-transform: uppercase;
  font-weight: 600;
  white-space: pre-wrap;
  display: inline-block;
  width: 140px;
  height: 23px;
  flex-shrink: 0;
}
.iconsBulletPoints,
.parent {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: space-between;
}
.parent {
  flex: 1;
  max-width: 1353.5792236328125px;
  text-align: center;
  font-size: 14.92px;
  color: var(--base-text);
  font-family: var(--font-lato);
}
.iconsBulletPoints {
  width: 100%;
  background-color: var(--white);
  padding: 99.95653533935547px 76.00861358642578px;
  box-sizing: border-box;
  max-width: 1509.761474609375px;
}
@media screen and (max-width: 1200px) {
  .parent {
    gap: var(--gap-xl);
  }
  .iconsBulletPoints {
    gap: var(--gap-mini);
  }
}
@media screen and (max-width: 960px) {
  .div,
  .div1,
  .div2,
  .parent {
    flex: unset;
    align-self: stretch;
  }
  .iconsBulletPoints,
  .parent {
    flex-direction: column;
  }
}
@media screen and (max-width: 420px) {
  .iconsBulletPoints {
    width: 400px;
  }
}
