.yourPreferedChoice {
  position: relative;
  line-height: 97.5px;
  text-transform: capitalize;
  font-weight: 900;
  display: inline-block;
  width: 569.25px;
}
.yourPreferedChoiceForSafetWrapper {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: center;
}
.bookNow {
  position: relative;
  letter-spacing: 3px;
  line-height: 19.5px;
  text-transform: uppercase;
}
.arrowRight {
  position: relative;
  width: 5.66px;
  height: 10.16px;
}
.btn {
  background: linear-gradient(#024273, #024273), #f24b4b;
  display: flex;
  flex-direction: row;
  padding: 15px var(--padding-5xl);
  align-items: center;
  justify-content: center;
  gap: var(--gap-5xs-5);
  font-size: var(--font-size-mini);
}
.frameParent,
.homeInner {
  display: flex;
  align-items: flex-start;
}
.frameParent {
  flex-direction: column;
  justify-content: center;
  gap: 37.5px;
}
.homeInner {
  align-self: stretch;
  overflow: hidden;
  flex-direction: row;
  padding: 9.896906852722168px;
  justify-content: flex-start;
  background-image: url(/public/frame-516@3x.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
}
.frameChild,
.servicesHeroImage {
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover;
}
.servicesHeroImage {
  align-self: stretch;
}
.frameChild {
  flex: 1;
  min-width: 350px;
}
.airportPickup {
  flex: 1;
  position: relative;
  line-height: 44.08px;
  font-weight: 600;
}
.subheader {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
}
.thePriceOf {
  flex: 1;
  position: relative;
  line-height: 36.6px;
}
.body {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
  font-size: 25.09px;
  font-family: var(--font-lato);
}
.frameGroup,
.subheaderParent {
  flex: 1;
  justify-content: flex-start;
}
.subheaderParent {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  gap: 38.51px;
  min-width: 350px;
  text-align: left;
  font-size: 33.44px;
  color: var(--base-text);
  font-family: var(--font-titillium-web);
}
.frameGroup {
  flex-direction: row;
  flex-wrap: wrap;
  gap: 63.84px;
  max-width: 1300px;
}
.frameGroup,
.home,
.servicesCard {
  display: flex;
  align-items: flex-start;
}
.servicesCard {
  align-self: stretch;
  background-color: var(--white);
  flex-direction: row;
  padding: 110.45742797851562px 109px 110.45742797851562px 28px;
  justify-content: space-between;
}
.home {
  position: relative;
  width: 100%;
  flex-direction: column;
  justify-content: flex-start;
  text-align: left;
  font-size: var(--font-size-53xl);
  color: var(--white);
  font-family: var(--font-titillium-web);
}
@media screen and (max-width: 1200px) {
  .home {
    flex-direction: column;
  }
}
@media screen and (max-width: 960px) {
  .subheaderParent {
    gap: var(--gap-6xl);
  }
  .frameGroup {
    flex: unset;
    align-self: stretch;
  }
  .servicesCard {
    flex-direction: column;
    gap: var(--gap-11xl);
  }
}
@media screen and (max-width: 420px) {
  .frameGroup {
    flex: unset;
    align-self: stretch;
  }
  .servicesCard {
    flex-direction: column;
    padding-left: var(--padding-11xl);
    padding-right: var(--padding-31xl);
    box-sizing: border-box;
  }
}
