}
@media screen and (max-width: 960px) {
  .cardAChild,
  .subheaderParent {
    flex: unset;
    align-self: stretch;
  }
  .subheaderParent {
    gap: var(--gap-6xl);
  }
  .cardA {
    flex-direction: column;
  }
  .subheaderGroup {
    gap: var(--gap-6xl);
    flex: unset;
    align-self: stretch;
  }
}
@media screen and (max-width: 420px) {
  .cardAChild,
  .subheaderParent {
    flex: unset;
    align-self: stretch;
  }
  .cardA {
    flex-direction: column;
  }
  .subheaderGroup {
    flex: unset;
    align-self: stretch;
  }
  .selfDriveOption {
    padding-left: var(--padding-xl);
    padding-right: var(--padding-xl);
    box-sizing: border-box;
  }
}
