.vivianArmah {
  position: relative;
  line-height: 43.5px;
  font-weight: 600;
}
.salesMarketing {
  position: relative;
  font-size: var(--font-size-2xs-5);
  letter-spacing: 0.23px;
  line-height: 18px;
  text-transform: uppercase;
  font-family: var(--font-lato);
}
.subheader {
  align-self: stretch;
  background-color: var(--color-primary);
  display: flex;
  flex-direction: column;
  padding: var(--padding-31xl) var(--padding-12xl);
  align-items: flex-start;
  justify-content: center;
  text-align: left;
  font-size: var(--font-size-14xl);
  color: var(--white);
  font-family: var(--font-titillium-web);
}
.biography {
  flex: 1;
  position: relative;
  line-height: 24px;
  font-weight: 600;
}
.subheader1 {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
}
.mrsVivianArmah {
  flex: 1;
  position: relative;
  line-height: 21px;
}
.body,
.frameParent,
.subheaderParent {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.body {
  align-self: stretch;
  flex-direction: row;
  font-size: var(--font-size-xs);
  font-family: var(--font-lato);
}
.frameParent,
.subheaderParent {
  flex: 1;
}
.subheaderParent {
  flex-direction: column;
  gap: 38.51px;
  min-width: 350px;
  text-align: left;
  font-size: var(--font-size-13xl);
  color: var(--base-text);
  font-family: var(--font-titillium-web);
}
.frameParent {
  flex-direction: row;
  flex-wrap: wrap;
  gap: 63.84px;
  max-width: 1300px;
}
.frameIcon,
.securityEscort {
  align-self: stretch;
  overflow: hidden;
}
.securityEscort {
  background-color: var(--white);
  display: flex;
  flex-direction: row;
  padding: 110.45742797851562px 101.33708953857422px var(--padding-15xl);
  align-items: flex-start;
  justify-content: space-between;
}
.frameIcon {
  max-width: 100%;
  max-height: 100%;
  object-fit: cover;
}
.vivian {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
@media screen and (max-width: 960px) {
  .subheaderParent {
    gap: var(--gap-6xl);
  }
  .frameParent {
    flex: unset;
    align-self: stretch;
  }
  .securityEscort {
    flex-direction: column;
    gap: var(--gap-11xl);
  }
}
@media screen and (max-width: 420px) {
  .frameParent {
    flex: unset;
    align-self: stretch;
  }
  .securityEscort {
    flex-direction: column;
    padding-left: var(--padding-31xl);
    padding-right: var(--padding-31xl);
    box-sizing: border-box;
  }
}
