.pictureIcon {
  align-self: stretch;
  border-radius: var(--br-xs);
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover;
}
.elmina {
  margin: 0;
}
.elminaCastle {
  align-self: stretch;
  position: relative;
  font-weight: 600;
}
.div {
  border-radius: var(--br-xs);
  background-color: var(--white);
  width: 224px;
  overflow: hidden;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  padding: var(--padding-5xl);
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
  gap: var(--gap-base);
  font-size: var(--font-size-lg);
  color: var(--color-black);
}
.larabangaMosque {
  align-self: stretch;
  position: relative;
  line-height: 28px;
  font-weight: 600;
}
.div1 {
  border-radius: var(--br-xs);
  background-color: var(--white);
  width: 224px;
  overflow: hidden;
  flex-shrink: 0;
  display: flex;
  flex-direction: column;
  padding: var(--padding-5xl);
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
  gap: var(--gap-base);
}
.parent,
.picFrametourPics {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  align-items: flex-start;
  justify-content: space-between;
}
.parent {
  flex: 1;
}
.picFrametourPics {
  width: 100%;
  background-color: var(--color-snow);
  padding: 98.32145690917969px 74.76527404785156px;
  box-sizing: border-box;
  max-width: 1485.06396484375px;
  text-align: center;
  font-size: var(--font-size-xl);
  color: var(--base-text);
  font-family: var(--font-titillium-web);
}
@media screen and (max-width: 1200px) {
  .picFrametourPics {
    gap: var(--gap-mini);
    padding-left: var(--padding-xl);
    padding-right: var(--padding-xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 960px) {
  .parent {
    flex: unset;
    align-self: stretch;
  }
  .picFrametourPics {
    flex-direction: column;
    padding: var(--padding-31xl) var(--padding-81xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 420px) {
  .picFrametourPics {
    width: 400px;
    padding-left: var(--padding-81xl);
    padding-right: var(--padding-xl);
    box-sizing: border-box;
    max-width: 1485%;
  }
}
