.meetgreetvisaArrangement {
  position: relative;
  line-height: 41.98px;
  font-weight: 600;
}
.homeServices {
  position: relative;
  font-size: 10.13px;
  letter-spacing: 0.22px;
  line-height: 17.37px;
  font-family: var(--t-16-medium);
}
.subheader {
  align-self: stretch;
  background-color: var(--color-primary);
  display: flex;
  flex-direction: column;
  padding: var(--padding-31xl) var(--padding-12xl);
  align-items: flex-start;
  justify-content: center;
  text-align: left;
  font-size: 31.85px;
  color: var(--white);
  font-family: var(--font-titillium-web);
}
.bannerIcon,
.frameChild {
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover;
}
.bannerIcon {
  align-self: stretch;
}
.frameChild {
  flex: 1;
  min-width: 350px;
}
.meetGreet {
  align-self: stretch;
  position: relative;
  line-height: 43.5px;
  font-weight: 600;
}
.weUnderstandThat {
  margin: 0;
}
.weUnderstandThatContainer {
  align-self: stretch;
  position: relative;
  font-size: var(--font-size-mini);
  line-height: 21px;
  font-weight: 600;
  font-family: var(--font-lato);
}
.meetGreetVisaArrangemenParent {
  flex: 1;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-mini);
  min-width: 350px;
}
.frameParent {
  flex: 1;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 81px;
  max-width: 1300px;
  text-align: left;
  font-size: var(--font-size-14xl);
  color: var(--base-text);
  font-family: var(--font-titillium-web);
}
.frameParent,
.meetandGreet,
.meetgreetvisaArrangement1 {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.meetgreetvisaArrangement1 {
  align-self: stretch;
  background-color: var(--white);
  overflow: hidden;
  flex-direction: row;
  flex-wrap: wrap;
  padding: 134px 95px;
}
.meetandGreet {
  position: relative;
  width: 100%;
  flex-direction: column;
}
@media screen and (max-width: 960px) {
  .frameParent {
    flex: unset;
    align-self: stretch;
  }
  .meetgreetvisaArrangement1 {
    flex-direction: column;
  }
}
@media screen and (max-width: 420px) {
  .meetgreetvisaArrangement1 {
    padding-left: var(--padding-31xl);
    padding-right: var(--padding-31xl);
    box-sizing: border-box;
  }
  .meetandGreet {
    padding-right: var(--padding-3xs);
    box-sizing: border-box;
  }
}
