.leaveUsYour {
  position: absolute;
  top: 0;
  left: 0;
  line-height: 58px;
  font-weight: 600;
}
.form,
.head {
  position: relative;
  width: 340px;
  height: 58px;
}
.form {
  width: 570px;
  height: 417px;
}
.writeReview {
  width: 570px;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  gap: var(--gap-26xl);
}
.contactUsSectionField,
.writeReviewWrapper {
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}
.writeReviewWrapper {
  flex: 1;
  flex-wrap: wrap;
  justify-content: flex-start;
}
.contactUsSectionField {
  align-self: stretch;
  overflow: hidden;
  padding: 110.45742797851562px var(--padding-34xl);
  justify-content: space-between;
  background-image: url(/public/contact-us-section-field@3x.png);
  background-size: cover;
  background-repeat: no-repeat;
  background-position: top;
  text-align: center;
  font-size: var(--font-size-25xl);
  color: var(--white);
  font-family: var(--font-titillium-web);
}
@media screen and (max-width: 960px) {
  .writeReviewWrapper {
    flex: unset;
    align-self: stretch;
  }
  .contactUsSectionField {
    flex-direction: column;
    gap: var(--gap-11xl);
  }
}
@media screen and (max-width: 420px) {
  .writeReviewWrapper {
    flex: unset;
    align-self: stretch;
  }
  .contactUsSectionField {
    flex-direction: column;
    padding-left: var(--padding-31xl);
    padding-right: var(--padding-31xl);
    box-sizing: border-box;
  }
}
