.shuttleServices {
  position: relative;
  line-height: 58px;
  font-weight: 600;
}
.homeServices {
  position: relative;
  font-size: 10.13px;
  letter-spacing: 0.22px;
  line-height: 17.37px;
  font-family: var(--t-16-medium);
}
.subheader {
  align-self: stretch;
  background-color: var(--color-primary);
  display: flex;
  flex-direction: column;
  padding: var(--padding-31xl);
  align-items: flex-start;
  justify-content: center;
  text-align: left;
  font-size: var(--font-size-25xl);
  color: var(--white);
  font-family: var(--font-titillium-web);
}
.bannerImageIcon {
  align-self: stretch;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover;
}
.shuttleServices1,
.suttelServices {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.shuttleServices1 {
  align-self: stretch;
  background-color: var(--white);
  overflow: hidden;
  flex-direction: row;
  flex-wrap: wrap;
  padding: var(--padding-81xl);
}
.suttelServices {
  position: relative;
  width: 100%;
  flex-direction: column;
}
@media screen and (max-width: 420px) {
  .shuttleServices1 {
    padding-left: var(--padding-xl);
    padding-right: var(--padding-xl);
    box-sizing: border-box;
  }
}
