.services1 {
  position: relative;
  line-height: 41.98px;
  font-weight: 600;
}
.homeServices {
  position: relative;
  font-size: 10.13px;
  letter-spacing: 0.22px;
  line-height: 17.37px;
  font-family: var(--t-16-medium);
}
.subheader {
  align-self: stretch;
  background-color: var(--color-primary);
  display: flex;
  flex-direction: column;
  padding: var(--padding-31xl) var(--padding-12xl);
  align-items: flex-start;
  justify-content: center;
  text-align: left;
  font-size: 31.85px;
  color: var(--white);
  font-family: var(--font-titillium-web);
}
.pictureIcon,
.selfDriveOptions {
  align-self: stretch;
  position: relative;
}
.pictureIcon {
  border-radius: 13.24px;
  max-width: 100%;
  overflow: hidden;
  height: 190.9px;
  flex-shrink: 0;
  object-fit: cover;
}
.selfDriveOptions {
  line-height: 35.31px;
  font-weight: 600;
}
.thisServiceIs {
  margin: 0;
}
.thisServiceIsContainer {
  align-self: stretch;
  position: relative;
  font-size: 15.45px;
  letter-spacing: 0.33px;
  line-height: 26.48px;
  font-family: var(--font-lato);
}
.content,
.selfDriveOptionsParent {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
.selfDriveOptionsParent {
  align-self: stretch;
  gap: 4.41px;
}
.content {
  flex: 1;
  gap: 26.48px;
}
.div,
.parent {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
}
.div {
  border-radius: 13.24px;
  background-color: var(--white);
  width: 335.46px;
  overflow: hidden;
  flex-shrink: 0;
  padding: 26.483888626098633px;
  box-sizing: border-box;
  align-items: flex-start;
}
.parent {
  flex: 1;
  flex-wrap: wrap;
  align-items: center;
}
.services,
.servicesCards {
  display: flex;
  align-items: flex-start;
}
.servicesCards {
  align-self: stretch;
  background-color: #f8f4f4;
  flex-direction: row;
  flex-wrap: wrap;
  padding: var(--padding-81xl) var(--padding-31xl);
  justify-content: space-between;
}
.services {
  position: relative;
  width: 100%;
  flex-direction: column;
  justify-content: flex-start;
  text-align: left;
  font-size: 22.07px;
  color: var(--base-text);
  font-family: var(--font-titillium-web);
}
@media screen and (max-width: 1200px) {
  .servicesCards {
    padding: var(--padding-81xl) var(--padding-31xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 960px) {
  .parent {
    flex: unset;
    align-self: stretch;
  }
  .servicesCards {
    flex-direction: column;
    padding-left: var(--padding-81xl);
    padding-right: var(--padding-81xl);
    box-sizing: border-box;
  }
}
@media screen and (max-width: 420px) {
  .servicesCards {
    padding-left: var(--padding-31xl);
    padding-right: var(--padding-31xl);
    box-sizing: border-box;
  }
}
