.tag,
.tagOrButton {
  position: relative;
}
.tag {
  border-radius: 100px;
  background-color: var(--color-darkslategray);
  border: 1px solid var(--color-darkslategray);
  display: flex;
  flex-direction: row;
  padding: 8px;
  align-items: flex-end;
  justify-content: center;
  text-align: left;
  font-size: var(--font-size-xs);
  color: var(--white);
  font-family: var(--font-source-code-pro);
}
