@keyframes fade-in {
  0% {
    opacity: 0;
  }
  to {
    opacity: 1;
  }
}
.oouimenu {
  position: relative;
  width: 20px;
  height: 20px;
  overflow: hidden;
  flex-shrink: 0;
}
.logoCopy1 {
  position: relative;
  width: 161px;
  height: 71px;
  object-fit: cover;
}
.vectorIcon {
  position: absolute;
  top: 0;
  left: 0;
  width: 18px;
  height: 14px;
}
.home,
.vectorWrapper {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: transparent;
  position: relative;
}
.vectorWrapper {
  width: 18px;
  height: 14px;
  display: none;
}
.home {
  border-radius: var(--br-3xs);
  width: 61px;
  height: 44px;
  opacity: 0;
}
.home.animate {
  animation: 1s ease 0s 1 normal forwards fade-in;
}
.home:hover {
  animation: 1s ease 0s 1 normal none fade-in;
  opacity: 1;
}
.home:active,
.home:hover {
  background-color: var(--color-primary);
}
.home:focus {
  background-color: var(--color-primary);
  border: 1px solid var(--color-primary);
  box-sizing: border-box;
}
.aboutUs1,
.aboutUsWrapper {
  cursor: pointer;
  border: 0;
  position: absolute;
}
.aboutUs1 {
  padding: 0;
  background-color: transparent;
  top: 10px;
  left: 10px;
  font-size: var(--t-16-medium-size);
  line-height: 24px;
  font-weight: 700;
  font-family: var(--font-titillium-web);
  color: var(--white);
  text-align: left;
  display: inline-block;
}
.aboutUsWrapper {
  padding: 3.238434314727783px;
  background-color: var(--color-primary);
  top: 13px;
  left: 5px;
  border-radius: 2.91px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  width: 82.26px;
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
}
.aboutUsWrapper:hover,
.ourTeamWrapper:hover {
  background-color: var(--color-slategray);
}
.ourTeam {
  position: relative;
  font-size: 10.69px;
  line-height: 14.09px;
  font-weight: 600;
  font-family: var(--font-titillium-web);
  color: var(--white);
  text-align: left;
}
.ourTeamWrapper,
.submenu {
  position: absolute;
  border-radius: 2.91px;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
}
.ourTeamWrapper {
  cursor: pointer;
  border: 0;
  padding: 3.238434314727783px;
  background-color: var(--color-primary);
  top: 52.14px;
  left: 4.53px;
  width: 82.26px;
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  align-items: center;
  justify-content: center;
}
.submenu {
  margin: 0;
  top: 49px;
  left: 6px;
  background-color: var(--white);
  width: 91px;
  height: 85.49px;
  display: none;
}
.aboutUs {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: transparent;
  position: relative;
  border-radius: var(--br-3xs);
  width: 85px;
  height: 44px;
  opacity: 0;
}
.aboutUs.animate {
  animation: 1s ease 0s 1 normal forwards fade-in;
}
.aboutUs:active,
.aboutUs:hover {
  background-color: var(--color-primary);
}
.aboutUs:active .submenu,
.aboutUs:hover .submenu {
  display: flex;
}
.aboutUs:focus {
  background-color: var(--color-primary);
}
.ourFleet {
  position: absolute;
  top: 10px;
  left: 10px;
  font-size: var(--t-16-medium-size);
  line-height: 24px;
  font-family: var(--font-titillium-web);
  color: var(--white);
  text-align: left;
}
.services {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: transparent;
  position: relative;
  width: 84px;
  height: 44px;
}
.services:hover {
  border-radius: var(--br-3xs);
}
.services:active,
.services:hover {
  background-color: var(--color-primary);
}
.services:focus {
  background-color: var(--color-primary);
}
.aboutUs3 {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: transparent;
  position: relative;
  border-radius: var(--br-3xs);
  width: 78px;
  height: 44px;
}
.aboutUs3:active,
.aboutUs3:hover {
  background-color: var(--color-primary);
}
.aboutUs3:focus {
  background-color: var(--color-primary);
  border-radius: var(--br-3xs);
}
.promo {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: transparent;
  position: relative;
  border-radius: var(--br-4xs);
  width: 66px;
  height: 44px;
}
.promo:active,
.promo:hover {
  background-color: var(--color-primary);
  border-radius: var(--br-3xs);
}
.promo:focus {
  background-color: var(--color-primary);
  border-radius: var(--br-3xs);
}
.contact {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: transparent;
  position: relative;
  border-radius: var(--br-3xs);
  width: 75px;
  height: 44px;
}
.contact:active,
.contact:hover {
  background-color: var(--color-primary);
}
.contact:focus {
  background-color: var(--color-primary);
}
.menu {
  position: absolute;
  top: 22px;
  left: 5px;
  width: 489px;
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: space-between;
}
.menuLink {
  text-decoration: none;
  width: 499px;
  height: 88px;
}
.b,
.menuLink,
.micallIcon {
  position: relative;
}
.micallIcon {
  width: 10px;
  height: 10px;
  overflow: hidden;
  flex-shrink: 0;
}
.b {
  flex: 1;
  line-height: 20.89px;
}
.callNumber {
  width: 156px;
  display: flex;
  flex-direction: row;
  padding: var(--padding-base);
  box-sizing: border-box;
  align-items: center;
  justify-content: flex-start;
  gap: var(--gap-11xs);
}
.fluentglobe24RegularIcon {
  position: relative;
  width: 16px;
  height: 16px;
  overflow: hidden;
  flex-shrink: 0;
}
.en {
  position: relative;
  line-height: 20.89px;
}
.lang,
.phoneAndLang {
  display: flex;
  flex-direction: row;
  box-sizing: border-box;
  align-items: center;
  justify-content: space-between;
}
.lang {
  width: 49px;
  padding: var(--padding-base) var(--padding-6xs);
  opacity: 0.7;
}
.phoneAndLang {
  width: 243px;
  padding: var(--padding-base) var(--padding-11xs);
}
.signin {
  position: relative;
  font-size: var(--font-size-lg);
  line-height: 24px;
  font-weight: 600;
  font-family: var(--font-titillium-web);
  color: var(--white);
  text-align: left;
}
.singinBtn,
.singupBtn {
  cursor: pointer;
  border: 0;
  padding: var(--padding-base) var(--padding-3xs);
  border-radius: var(--br-6xl);
  justify-content: flex-start;
}
.singinBtn {
  background-color: var(--color-darkslateblue-100);
  display: flex;
  flex-direction: row;
  align-items: flex-start;
}
.singinBtn:hover {
  background-color: var(--color-steelblue-100);
}
.singupBtn {
  background-color: transparent;
  background: linear-gradient(#024273, #024273), #fff;
}
.buttons,
.headerNavbar,
.singupBtn {
  display: flex;
  flex-direction: row;
  align-items: center;
}
.buttons {
  padding: var(--padding-base) var(--padding-3xs);
  justify-content: flex-start;
  gap: var(--gap-11xs);
}
.headerNavbar {
  margin: 0;
  align-self: stretch;
  background-color: var(--color-darkorange);
  padding: var(--padding-base) var(--padding-47xl);
  justify-content: space-between;
  text-align: left;
  font-size: 13.43px;
  color: var(--white);
  font-family: var(--font-titillium-web);
}
@media screen and (max-width: 1200px) {
  .vectorWrapper {
    display: none;
  }
}
@media screen and (max-width: 960px) {
  .vectorWrapper {
    display: flex;
  }
  .menuLink,
  .submenu {
    display: none;
  }
  .phoneAndLang {
    display: none;
    width: 243px;
  }
  .buttons {
    display: none;
  }
}
@media screen and (max-width: 420px) {
  .logoCopy1 {
    display: flex;
  }
  .buttons,
  .phoneAndLang {
    display: none;
  }
}
