@keyframes slide-in-left {
  0% {
    transform: translateX(-200px);
    opacity: 0;
  }
  to {
    transform: translateX(0);
    opacity: 1;
  }
}
.aboutUs1 {
  cursor: pointer;
  border: 0;
  padding: 0;
  background-color: transparent;
  position: relative;
  font-size: var(--font-size-xl);
  line-height: 43.27px;
  font-weight: 600;
  font-family: var(--font-titillium-web);
  color: var(--color-gray-100);
  text-align: left;
  display: inline-block;
}
.aboutUs,
.ourTeam {
  display: flex;
  flex-direction: row;
  padding: var(--padding-3xs);
  align-items: center;
  justify-content: center;
  cursor: pointer;
  z-index: 0;
}
.aboutUs:active,
.aboutUs:hover {
  background-color: var(--color-darkorange);
}
.ourTeam {
  z-index: 1;
}
.ourFleet {
  z-index: 2;
}
.contact,
.home,
.ourFleet,
.promo,
.services {
  display: flex;
  flex-direction: row;
  padding: var(--padding-3xs);
  align-items: center;
  justify-content: center;
}
.services {
  cursor: pointer;
  z-index: 3;
}
.contact,
.home,
.promo {
  z-index: 4;
}
.contact,
.home {
  cursor: pointer;
  z-index: 5;
}
.home {
  border: 0;
  background-color: transparent;
  margin: 0 !important;
  position: absolute;
  top: -19px;
  left: 16.5px;
  z-index: 6;
}
.home:active,
.home:hover {
  border-radius: var(--br-3xs);
}
.home:active {
  background-color: var(--color-darkorange);
}
.home:focus {
  background-color: var(--white);
}
.aboutUsParent {
  position: absolute;
  top: 126px;
  left: 113px;
  width: 102px;
  height: 429px;
  display: flex;
  flex-direction: column;
  padding: var(--padding-21xl) 0 0;
  box-sizing: border-box;
  align-items: center;
  justify-content: space-between;
}
.login,
.loginWrapper {
  cursor: pointer;
  border: 0;
}
.login {
  padding: 0;
  background-color: transparent;
  position: relative;
  font-size: var(--font-size-xl);
  line-height: 43.27px;
  font-weight: 600;
  font-family: var(--font-titillium-web);
  color: var(--color-gray-100);
  text-align: center;
  display: inline-block;
}
.loginWrapper {
  padding: var(--padding-3xs);
  background-color: var(--color-sandybrown);
  border-radius: var(--br-3xs);
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25), 0 4px 4px rgba(0, 0, 0, 0.25);
  align-items: center;
  justify-content: center;
}
.loginWrapper:hover {
  background-color: #bbaa90;
}
.frameParent,
.loginWrapper,
.signupWrapper {
  display: flex;
  flex-direction: row;
}
.signupWrapper {
  cursor: pointer;
  border: 0;
  padding: var(--padding-3xs);
  background-color: var(--color-sandybrown);
  border-radius: var(--br-3xs);
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.25);
  align-items: center;
  justify-content: center;
}
.signupWrapper:hover {
  background-color: #d0b289;
}
.frameParent {
  top: 19px;
  left: 64px;
  align-items: flex-start;
  justify-content: flex-start;
  gap: 53px;
}
.b,
.frameParent,
.wrapper {
  position: absolute;
}
.b {
  top: 0;
  left: 0;
  line-height: 21px;
}
.wrapper {
  top: 584px;
  left: 101px;
  width: 125px;
  height: 21px;
}
.drawerMenu {
  position: relative;
  background-color: var(--color-primary);
  width: 327px;
  height: 100%;
  opacity: 0;
  max-width: 90%;
  overflow: auto;
  text-align: center;
  font-size: var(--font-size-mini);
  color: var(--white);
  font-family: var(--font-titillium-web);
}
.drawerMenu.animate {
  animation: 0.25s ease 0s 1 normal forwards slide-in-left;
}
@media screen and (max-width: 960px) {
  .aboutUs:hover {
    background-color: var(--color-darkorange);
  }
  .aboutUs:active,
  .contact:active,
  .contact:hover,
  .ourFleet:active,
  .ourFleet:hover,
  .ourTeam:active,
  .ourTeam:hover,
  .promo:active,
  .promo:hover,
  .services:active,
  .services:hover {
    background-color: var(--color-darkorange);
    border-radius: var(--br-3xs);
  }
  .ourTeam {
    border-radius: 0;
  }
  .home:hover {
    background-color: var(--color-darkorange);
  }
}
