.cardAChild {
  flex: 1;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover;
  min-width: 342.1588439941406px;
}
.airportTransit {
  flex: 1;
  position: relative;
  line-height: 47.52px;
  font-weight: 600;
}
.subheader {
  align-self: stretch;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: flex-start;
}
.ourAirportShuttle {
  flex: 1;
  position: relative;
  line-height: 22.94px;
}
.body {
  align-self: stretch;
  flex-direction: row;
  font-size: 19.66px;
  font-family: var(--font-lato);
}
.body,
.cardA,
.subheaderParent {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
}
.subheaderParent {
  flex: 1;
  flex-direction: column;
  gap: 41.51px;
  min-width: 300px;
  text-align: left;
  font-size: 36.05px;
  color: var(--base-text);
  font-family: var(--font-titillium-web);
}
.cardA {
  width: 100%;
  flex-direction: row;
  flex-wrap: wrap;
  gap: 68.82px;
  max-width: 1270.875732421875px;
}
@media screen and (max-width: 960px) {
  .cardAChild,
  .subheaderParent {
    flex: unset;
    align-self: stretch;
  }
  .subheaderParent {
    gap: var(--gap-6xl);
  }
  .cardA {
    flex-direction: column;
  }
}
@media screen and (max-width: 420px) {
  .cardAChild,
  .subheaderParent {
    flex: unset;
    align-self: stretch;
  }
  .cardA {
    flex-direction: column;
  }
}
