.chauffeurDrivenDay {
  position: relative;
  line-height: 58px;
  font-weight: 600;
}
.homeServices {
  position: relative;
  font-size: 10.13px;
  letter-spacing: 0.22px;
  line-height: 17.37px;
  font-family: var(--t-16-medium);
}
.subheader {
  align-self: stretch;
  background-color: var(--color-primary);
  display: flex;
  flex-direction: column;
  padding: var(--padding-31xl);
  align-items: flex-start;
  justify-content: center;
  text-align: left;
  font-size: var(--font-size-25xl);
  color: var(--white);
  font-family: var(--font-titillium-web);
}
.bannerImageIcon {
  align-self: stretch;
  max-width: 100%;
  overflow: hidden;
  max-height: 100%;
  object-fit: cover;
}
.chauffeurDrivenDayTrip {
  position: relative;
  width: 100%;
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
}
